.home {
    display: flex;
    justify-content: center;
    margin-left: auto;
    margin-right: auto;
    max-width: 1500px;
    flex-wrap: wrap;

  }
  
  .home__row {
    display: flex;
    z-index: 1;
    margin-left: 5px;
    margin-right: 5px;
  }
  
  .home__image {
    width: 100%;
    z-index: -1;
    margin-bottom: -280px;
    mask-image: linear-gradient( to bottom , rgb(0, 0, 0 ,1) , rgba(0, 0, 0, 0.95) );
}